@import "https://fonts.googleapis.com/css2?family=Arya:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e6e6e6;
  min-height: 100vh;
  font-family: Arya, sans-serif;
}

h1 {
  text-align: center;
  letter-spacing: 2px;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.loading-container {
  background: #fffc;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.loader {
  width: 80px;
  height: 80px;
  position: absolute;
}

.loader .loading-bar {
  box-sizing: border-box;
  border: 8px solid #0000;
  border-top-color: #7d7de8;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  margin: 8px;
  animation: 1.2s cubic-bezier(.5, 0, .5, 1) infinite loading;
  display: block;
  position: absolute;
}

.loader .loading-bar:first-child {
  animation-delay: -.45s;
}

.loader .loading-bar:nth-child(2) {
  animation-delay: -.3s;
}

.loader .loading-bar:nth-child(3) {
  animation-delay: -.15s;
}

@keyframes loading {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.image-container {
  grid-template-columns: repeat(auto-fill, 300px);
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: 1rem;
  display: grid;
}

.image-container img {
  width: 100%;
  max-height: 300px;
  margin-top: .5rem;
}

.image-container .img-link:hover {
  transition: transform .3s;
  transform: scale(1.1);
}

.image-container .img-link {
  background-color: #adebeb80;
}

.hidden {
  display: none;
}

.error-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.error {
  color: red;
  text-align: center;
  margin-top: 1rem;
}

.error-image {
  width: 100%;
  height: 300px;
}

.retry-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(#52e0e0, #1fadad);
  border: none;
  border-radius: 5px;
  margin-top: 1rem;
  padding: 1rem 2rem;
  font-size: 1.25rem;
  font-weight: bold;
  box-shadow: inset 0 0 5px #00000080;
}

.retry-button:hover {
  background: linear-gradient(#26d9d9, #178282);
}

.retry-button:active {
  transform: translateY(2px);
}

@media screen and (width <= 600px) {
  h1 {
    font-size: 1.25rem;
  }

  .image-container {
    margin: .825rem;
  }
}
